<template>
  <div class="mobile-game-config">
    <!-- 顶部标题栏 -->
    <header class="header-bar">
      <button class="back-btn" @click="goBack">
        <svg viewBox="0 0 24 24">
          <path d="M15.41,16.58L10.83,12L15.41,7.41L14,6L8,12L14,18L15.41,16.58Z"/>
        </svg>
      </button>
      <h1 class="header-title">运行配置</h1>
      <div class="header-placeholder"></div>
    </header>

    <!-- 主内容区 -->
    <main class="main-content">
      <form @submit.prevent="submitForm" class="config-form">
        <!-- 服务区选择 -->
        <div class="form-section">
          <label class="section-label">选择服务区</label>
          <div class="select-trigger" @click="openPicker('server')">
            <span class="selected-value">{{ form.server || '请选择服务区' }}</span>
            <svg class="trigger-icon" viewBox="0 0 24 24">
              <path d="M7,10L12,15L17,10H7Z"/>
            </svg>
          </div>
        </div>

        <!-- 角色数量 -->
        <div class="form-section">
          <label class="section-label">角色运行个数</label>
          <div class="number-stepper">
            <button type="button" class="stepper-btn" @click="decrementCount" :disabled="form.characterCount <= 1">
              <svg viewBox="0 0 24 24">
                <path d="M19,13H5V11H19V13Z"/>
              </svg>
            </button>
            <span class="stepper-value">{{ form.characterCount }}</span>
            <button type="button" class="stepper-btn" @click="incrementCount" :disabled="form.characterCount >= 5">
              <svg viewBox="0 0 24 24">
                <path d="M19,13H13V19H11V13H5V11H11V5H13V11H19V13Z"/>
              </svg>
            </button>
          </div>
        </div>

        <!-- 额外功能设置 -->
        <div class="form-section">
          <label class="section-label">额外功能设置</label>
          <div class="extra-options">
            <div class="option-item">
              <label class="option-label">
                <input type="checkbox" v-model="form.groupSignIn" class="option-checkbox">
                <span class="option-text">群签到</span>
              </label>
            </div>
            <div class="option-item">
              <label class="option-label">
                <input type="checkbox" v-model="form.buyWelfare" class="option-checkbox">
                <span class="option-text">购买福利</span>
              </label>
            </div>
            <div class="option-item">
              <label class="option-label">
                <input type="checkbox" v-model="form.buyBloodPack" class="option-checkbox">
                <span class="option-text">购买药水</span>
              </label>
            </div>
            <div class="option-item">
              <label class="option-label">
                <input type="checkbox" v-model="form.switchEquipment" class="option-checkbox">
                <span class="option-text">切换装备</span>
              </label>
            </div>
            <!-- 新增禁用选项 -->
            <div class="option-item disabled">
              <label class="option-label">
                <input type="checkbox" readonly class="option-checkbox" checked>
                <span class="option-text">金币复活</span>
              </label>
            </div>
            <div class="option-item disabled">
              <label class="option-label">
                <input type="checkbox" readonly class="option-checkbox" checked>
                <span class="option-text">领取邮件(角1生效)</span>
              </label>
            </div>
          </div>
        </div>

        <!-- 挂机设置 -->
        <div class="form-section">
          <label class="section-label">挂机设置</label>
          <div class="select-trigger" @click="openPicker('afkMode')">
            <span class="selected-value">{{ getAfkModeLabel(form.afkMode) }}</span>
            <svg class="trigger-icon" viewBox="0 0 24 24">
              <path d="M7,10L12,15L17,10H7Z"/>
            </svg>
          </div>
        </div>

        <!-- 副本选择 - 多选 -->
        <div class="form-section">
          <label class="section-label">选择副本 (可多选)</label>
          <div class="dungeon-grid">
            <button 
              type="button"
              v-for="dungeon in dungeons" 
              :key="dungeon.id" 
              class="dungeon-btn"
              :class="{ active: isDungeonSelected(dungeon.id) }"
              @click="toggleDungeon(dungeon.id)"
            >
              {{ dungeon.name }}
            </button>
          </div>
        </div>

        <!-- 动态渲染每个选中副本的难度选项 -->
        <div 
          class="form-section" 
          v-for="dungeonId in form.selectedDungeons" 
          :key="`difficulty-${dungeonId}`"
        >
          <label class="section-label">{{ getDungeonName(dungeonId) }} - 难度</label>
          <div class="select-trigger" @click="openDifficultyPicker(dungeonId)">
            <span class="selected-value">{{ getCurrentDifficultyLabel(dungeonId) }}</span>
            <svg class="trigger-icon" viewBox="0 0 24 24">
              <path d="M7,10L12,15L17,10H7Z"/>
            </svg>
          </div>
        </div>

        <!-- 提交按钮 -->
        <button type="submit" class="submit-btn">
          <svg class="btn-icon" viewBox="0 0 24 24">
            <path d="M17,3H5A2,2 0 0,0 3,5V19A2,2 0 0,0 5,21H19A2,2 0 0,0 21,19V7L17,3M12,19A4,4 0 0,1 8,15A4,4 0 0,1 12,11A4,4 0 0,1 16,15A4,4 0 0,1 12,19M15,9H9V5H15V9Z"/>
          </svg>
          保存配置 ({{ form.selectedDungeons.length }}个副本)
        </button>
      </form>
    </main>

    <!-- 底部操作栏 -->
    <footer class="bottom-bar">
      <button class="action-btn" @click="clearForm">
        <svg viewBox="0 0 24 24">
          <path d="M19,4H15.5L14.5,3H9.5L8.5,4H5V6H19M6,19A2,2 0 0,0 8,21H16A2,2 0 0,0 18,19V7H6V19Z"/>
        </svg>
        清空
      </button>
      <button class="action-btn primary" @click="submitForm">
        <svg viewBox="0 0 24 24">
          <path d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z"/>
        </svg>
        提交
      </button>
    </footer>

    <!-- 遮罩层 -->
    <transition name="fade">
      <div v-if="showPicker" class="modal-mask" @click.self="cancelPicker"></div>
    </transition>

    <!-- 底部选择器弹窗 -->
    <transition name="slide-up">
      <div v-if="showPicker" class="bottom-picker">
        <div class="picker-header">
          <button class="picker-cancel" @click="cancelPicker">取消</button>
          <h3 class="picker-title">{{ pickerTitle }}</h3>
          <button class="picker-confirm" @click="confirmPicker">确定</button>
        </div>
        <div class="picker-content">
          <div 
            v-for="(item, index) in pickerItems" 
            :key="item.id || index" 
            class="picker-item"
            :class="{ active: isPickerItemActive(item) }"
            @click="selectPickerItem(item)"
          >
            {{ item.label || item }}
          </div>
        </div>
      </div>
    </transition>

    <!-- 通知提示 -->
    <transition name="fade">
      <div v-if="showToast" class="toast" :class="toastType">
        <svg class="toast-icon" viewBox="0 0 24 24">
          <path v-if="toastType === 'success'" d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z" />
          <path v-if="toastType === 'error'"
            d="M19,6.41L17.59,5L12,10.59L6.41,5L5,6.41L10.59,12L5,17.59L6.41,19L12,13.41L17.59,19L19,17.59L13.41,12L19,6.41Z" />
        </svg>
        <span class="toast-message">{{ toastMessage }}</span>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'MobileMultiDungeonConfig',
  data() {
    return {
      servers: ['阿萨01', '阿萨02', '阿萨03', '阿萨04', '阿萨05', '阿萨04', '华纳01', '华纳02', '华纳03', '华纳04', '华纳05', '华纳04'],
      afkModes: [
        { value: 'none', label: '不挂机' },
        { value: 'normal', label: '挂机点1' },
        { value: 'intensive', label: '挂机点2' },
        { value: 'custom', label: '挂机点3' }
      ],
      dungeons: [
        {
          id: 'd1', 
          name: '剧毒之蛇巢穴',
          difficulties: ['普通', '困难', '极难', '极恶']
        },
        { 
          id: 'd2', 
          name: '被遗忘的巨人洞穴',
          difficulties: ['普通', '困难', '极难', '极恶']
        },
        { 
          id: 'd3', 
          name: '侏儒王室的坟墓',
          difficulties: ['普通', '困难', '极难', '极恶']
        },
        { 
          id: 'd4', 
          name: '亡者码头',
          difficulties: ['普通', '困难', '极难', '极恶']
        },
        { 
          id: 'd5', 
          name: '虚无遗址',
          difficulties: ['第2阶段','第3阶段','第4阶段','第5阶段','第6阶段','第7阶段','第8阶段']
        },
        { 
          id: 'd6', 
          name: '侏儒密道',
          difficulties: ['第2阶段','第3阶段','第4阶段','第5阶段','第7阶段','第8阶段']
        }
      ],
      form: {
        server: '',
        characterCount: 1,
        groupSignIn: false,
        buyWelfare: false,
        buyBloodPack: false,
        switchEquipment: false,
        afkMode: 'none',
        selectedDungeons: [],
        dungeonDifficulties: []
      },
      // 选择器相关状态
      showPicker: false,
      pickerType: '',
      pickerTitle: '',
      pickerItems: [],
      tempPickerValue: null,
      currentDungeonId: null,
      // 通知相关
      showToast: false,
      toastMessage: '',
      toastType: 'success'
    };
  },
  created() {
    this.loadFormData();
  },
  methods: {
    // 导航
    goBack() {
      this.$goBackOrHome()
    },
    
    // 角色数量控制
    incrementCount() {
      if (this.form.characterCount < 10) {
        this.form.characterCount++;
      }
    },
    decrementCount() {
      if (this.form.characterCount > 1) {
        this.form.characterCount--;
      }
    },
    
    // 副本选择
    isDungeonSelected(dungeonId) {
      return this.form.selectedDungeons.includes(dungeonId);
    },
    toggleDungeon(dungeonId) {
      const index = this.form.selectedDungeons.indexOf(dungeonId);
      if (index === -1) {
        this.form.selectedDungeons.push(dungeonId);
        this.setDungeonDifficulty(dungeonId, 0);
      } else {
        this.form.selectedDungeons.splice(index, 1);
        this.form.dungeonDifficulties = this.form.dungeonDifficulties.filter(
          d => d.dungeonId !== dungeonId
        );
      }
    },
    getDungeonName(dungeonId) {
      const dungeon = this.dungeons.find(d => d.id === dungeonId);
      return dungeon ? dungeon.name : '';
    },
    
    // 难度选择
    getCurrentDifficultyLabel(dungeonId) {
      const difficulty = this.form.dungeonDifficulties.find(d => d.dungeonId === dungeonId);
      const dungeon = this.dungeons.find(d => d.id === dungeonId);
      if (!difficulty || !dungeon) return '请选择难度';
      return dungeon.difficulties[difficulty.index] || '请选择难度';
    },
    setDungeonDifficulty(dungeonId, index) {
      const difficultyIndex = this.form.dungeonDifficulties.findIndex(d => d.dungeonId === dungeonId);
      if (difficultyIndex >= 0) {
        this.form.dungeonDifficulties[difficultyIndex].index = index;
      } else {
        this.form.dungeonDifficulties.push({
          dungeonId,
          index
        });
      }
    },
    
    // 挂机模式标签
    getAfkModeLabel(value) {
      const mode = this.afkModes.find(m => m.value === value);
      return mode ? mode.label : '请选择挂机模式';
    },
    
    // 选择器控制
    openPicker(type) {
      this.pickerType = type;
      
      switch (type) {
        case 'server':
          this.pickerTitle = '选择服务区';
          this.pickerItems = this.servers;
          this.tempPickerValue = this.form.server;
          break;
          
        case 'afkMode':
          this.pickerTitle = '选择挂机模式';
          this.pickerItems = this.afkModes;
          this.tempPickerValue = this.form.afkMode;
          break;
          
        default:
          return;
      }
      
      this.showPicker = true;
    },
    
    openDifficultyPicker(dungeonId) {
      this.currentDungeonId = dungeonId;
      this.pickerType = 'difficulty';
      this.pickerTitle = `选择${this.getDungeonName(dungeonId)}难度`;
      
      const dungeon = this.dungeons.find(d => d.id === dungeonId);
      if (dungeon) {
        this.pickerItems = dungeon.difficulties.map((label, index) => ({
          label,
          value: index
        }));
      }
      
      const currentDiff = this.form.dungeonDifficulties.find(d => d.dungeonId === dungeonId);
      this.tempPickerValue = currentDiff ? currentDiff.index : null;
      
      this.showPicker = true;
    },
    
    isPickerItemActive(item) {
      if (this.pickerType === 'afkMode') {
        return this.tempPickerValue === item.value;
      }
      if (this.pickerType === 'difficulty') {
        return this.tempPickerValue === item.value;
      }
      return this.tempPickerValue === item;
    },
    
    selectPickerItem(item) {
      if (this.pickerType === 'afkMode') {
        this.tempPickerValue = item.value;
      } else if (this.pickerType === 'difficulty') {
        this.tempPickerValue = item.value;
      } else {
        this.tempPickerValue = item;
      }
    },
    
    confirmPicker() {
      switch (this.pickerType) {
        case 'server':
          if (this.tempPickerValue) {
            this.form.server = this.tempPickerValue;
          }
          break;
          
        case 'afkMode':
          if (this.tempPickerValue) {
            this.form.afkMode = this.tempPickerValue;
          }
          break;
          
        case 'difficulty':
          if (this.currentDungeonId && this.tempPickerValue !== null) {
            this.setDungeonDifficulty(this.currentDungeonId, this.tempPickerValue);
          }
          break;
      }
      
      this.showPicker = false;
    },
    
    cancelPicker() {
      this.showPicker = false;
    },
    
    // 表单操作
    clearForm() {
      this.form = {
        server: '',
        characterCount: 1,
        groupSignIn: false,
        buyWelfare: false,
        buyBloodPack: false,
        switchEquipment: false,
        afkMode: 'none',
        selectedDungeons: [],
        dungeonDifficulties: []
      };
      localStorage.removeItem('dungeonConfig');
      this.showNotification('已清空表单', 'success');
    },
    
    submitForm() {
      if (!this.form.server) {
        this.showNotification('请选择服务区', 'error');
        return;
      }
      
      if (this.form.selectedDungeons.length === 0) {
        this.showNotification('请至少选择一个副本', 'error');
        return;
      }
      
      for (const dungeonId of this.form.selectedDungeons) {
        if (!this.form.dungeonDifficulties.some(d => d.dungeonId === dungeonId)) {
          this.showNotification(`请设置${this.getDungeonName(dungeonId)}的难度`, 'error');
          return;
        }
      }
      
      this.saveFormData();
      
      const submitData = {
        server: this.servers.indexOf(this.form.server),
        characterCount: this.form.characterCount,
        groupSignIn: this.form.groupSignIn,
        buyWelfare: this.form.buyWelfare,
        buyBloodPack: this.form.buyBloodPack,
        switchEquipment: this.form.switchEquipment,
        afkMode: this.form.afkMode,
        selectedDungeons: this.form.selectedDungeons.map(id => 
          this.dungeons.findIndex(d => d.id === id)
        ),
        dungeonDifficulties: this.form.dungeonDifficulties.map(d => ({
          dungeonIndex: this.dungeons.findIndex(item => item.id === d.dungeonId),
          difficultyIndex: d.index
        }))
      };
      
      this.showNotification('配置保存成功', 'success');
      console.log('配置已提交:', submitData);
    },
    
    showNotification(message, type = 'success') {
      this.toastMessage = message;
      this.toastType = type;
      this.showToast = true;
      
      setTimeout(() => {
        this.showToast = false;
      }, 3000);
    },
    
    saveFormData() {
      const saveData = {
        server: this.servers.indexOf(this.form.server),
        characterCount: this.form.characterCount,
        groupSignIn: this.form.groupSignIn,
        buyWelfare: this.form.buyWelfare,
        buyBloodPack: this.form.buyBloodPack,
        switchEquipment: this.form.switchEquipment,
        afkMode: this.form.afkMode,
        selectedDungeons: this.form.selectedDungeons.map(id => 
          this.dungeons.findIndex(d => d.id === id)
        ),
        dungeonDifficulties: this.form.dungeonDifficulties.map(d => ({
          dungeonIndex: this.dungeons.findIndex(item => item.id === d.dungeonId),
          difficultyIndex: d.index
        }))
      };
      localStorage.setItem('dungeonConfig', JSON.stringify(saveData));
    },
    
    loadFormData() {
      const savedData = localStorage.getItem('dungeonConfig');
      if (savedData) {
        try {
          const parsedData = JSON.parse(savedData);
          
          this.form = {
            server: this.servers[parsedData.server] || '',
            characterCount: parsedData.characterCount || 1,
            groupSignIn: parsedData.groupSignIn || false,
            buyWelfare: parsedData.buyWelfare || false,
            buyBloodPack: parsedData.buyBloodPack || false,
            switchEquipment: parsedData.switchEquipment || false,
            afkMode: parsedData.afkMode || 'none',
            selectedDungeons: parsedData.selectedDungeons.map(index => 
              this.dungeons[index]?.id || ''
            ).filter(Boolean),
            dungeonDifficulties: parsedData.dungeonDifficulties.map(d => ({
              dungeonId: this.dungeons[d.dungeonIndex]?.id || '',
              index: d.difficultyIndex
            })).filter(d => d.dungeonId)
          };
        } catch (e) {
          console.error('加载保存的配置失败:', e);
        }
      }
    }
  }
};
</script>

